import { h, Component } from 'preact';
import util from 'preact-util';
import { observer } from 'mobx-preact';
import linkState from 'linkstate';
import { route } from 'preact-router';
import { toJS } from 'mobx';
import { Text, Localizer } from 'preact-i18n';

import mu from '../../lib/musher-util';
const MARGIN_TOP = mu.marginTop(false);
const MARGIN_TOP_BACK = mu.marginTopBack(false);
const MARGIN_BOTTOM = mu.marginBottom();

import Images from '../../components/form/images';
import Files from '../../components/form/files';
import Map from '../../components/map';

import ScrollIntoView from '../../components/util/scrollIntoView';

const initialState = {
    newTrack: {},
};

@observer
class Input extends Component {
    handleImageErrored = (e) => {
        const image = e.target;
        image.onerror = null;
        setTimeout(() => {
            image.src += `?${new Date()}`;
        }, 1000);
    }

    setDateToNow = () => {
        const { field, updateField, track } = this.props;
        const value = util.isoDate(undefined, false, false, true);
        // updateFieldValue = ({ id, field, value, datatype, elementtype, type, isNew = false, skipTimer = false }) => {
        updateField({ id: track.id, field, value });
    }

    setTimeToNow = () => {
        const { field, updateField, track } = this.props;
        const value = util.isoTime();
        // updateFieldValue = ({ id, field, value, datatype, elementtype, type, isNew = false, skipTimer = false }) => {
        updateField({ id: track.id, field, value });
    }

    removeImage = async (e) => {
        const { trackStore } = this.props.stores;
        const { track } = this.props;
        const imageName = e.target.parentElement.dataset.name || e.target.dataset.name;
        trackStore.removeImage({
            id: track.id,
            name: imageName,
        });
    }

    removeMainImage = () => {
        const { track, field, updateField } = this.props;
        updateField({ id: track.id, field, value: {} });
    }

    render() {
        // const { saved, dog, type = 'text', datalist = [], min, max, step, field, title, icon, help, onInput, updateField } = this.props;
        const { saved, track, type = 'text', field, values = [], title, icon, help, onInput, updateField } = this.props;
        let value = toJS(track[field]);
        if (type === 'date') {
            if (value) {
                value = util.isoDate(track[field], false, false, true);
            }
        }
        if (type === 'image') {
            return (
                <div class={`form-group ${saved[`${field}.${track.id}`] ? 'is-valid' : ''}`}>
                    <label for={`${field}Input`}>{icon && <i class={`${icon} text-muted`} />} {title}</label>

                    <div class='container-fluid'>
                        <div class='row'>
                            {track.image && track.image.s3SmallLink && <div class='col-3 p-1'>
                                <img src={track.image.s3SmallLink} class='img-fluid border rounded-lg' onError={this.handleImageErrored} />
                                <button class='btn btn-sm btn-link'
                                    onClick={this.removeMainImage}
                                    data-name={track.image.name}
                                ><i class='fas fa-trash' /> Del</button>
                                <span class='badge badge-success'>Main</span>
                            </div>}
                            {track.images && track.images.map(img => (
                                <div class='col-3 p-1'>
                                    <img src={img.s3SmallLink} class='img-fluid' onError={this.handleImageErrored} />
                                    <button class='btn btn-sm btn-link'
                                        onClick={this.removeImage}
                                        data-name={img.name}
                                    ><i class='fas fa-trash' /> Del</button>
                                </div>
                            ))}
                        </div>
                    </div>
                    <Images saved={saved} object={track} field={field} updateField={updateField} autoOpen={false} />
                    {saved[`${field}.${track.id}`] ? <div class={`valid-feedback`} style='display: block;'><Text id='tracks.changes-saved'>Endringene er lagret</Text></div> : 
                        <small id={`${field}Help`} class='form-text text-muted'>{help}</small>}
                </div>
            );
        }
        if (type === 'file') {
            return (
                <div class={`form-group ${saved[`${field}.${track.id}`] ? 'is-valid' : ''}`}>
                    <label for={`${field}Input`}>{icon && <i class={`${icon} text-muted`} />} {title}</label>
                    <Files saved={saved} object={track} field={field} updateField={updateField} />
                    {saved[`${field}.${track.id}`] ? <div class={`valid-feedback`} style='display: block;'><Text id='tracks.changes-saved'>Endringene er lagret</Text></div> : 
                        <small id={`${field}Help`} class='form-text text-muted'>{help}</small>}
                </div>
            );
        }
        if (type === 'radio') {
            return (
                <div class={`form-group ${saved[`${field}.${track.id}`] ? 'is-valid' : ''}`}>
                    <label for={`${field}Input`}>{icon && <i class={`${icon} text-muted`} />} {title} "{value}"</label>
                    {values.map(option => <>
                        <div class='form-check'>
                            <input
                                class={`form-check-input ${saved[`${field}.${track.id}`] ? 'is-valid' : ''}`}
                                type='radio'
                                id={`${field}Input`}
                                value={option[0]}
                                data-id={track.id}
                                data-field={field}
                                onInput={onInput}
                                checked={value == option[0] ? 'checked' : ''}
                            />
                            <label class='form-check-label' for={`${field}Input`}>
                                {option[1]}
                            </label>
                        </div>
                    </>)}
                    {saved[`${field}.${track.id}`] ? <div class={`valid-feedback`} style='display: block;'><Text id='tracks.changes-saved'>Endringene er lagret</Text></div> : 
                        <small id={`${field}Help`} class='form-text text-muted'>{help}</small>}
                </div>
            );
        }
        if (type === 'toggle') {
            return (
                <div class={`form-group ${saved[`${field}.${track.id}`] ? 'is-valid' : ''}`}>
                    <label for={`${field}Input`}>{icon && <i class={`${icon} text-muted`} />} {title}</label>
                    <div class='custom-control custom-switch'>
                        <input type='checkbox'
                            class='custom-control-input'
                            id={`${field}Input`}
                            value={value === 1 ? 0 : 1}
                            data-id={track.id}
                            data-field={field}
                            onInput={onInput}
                            checked={value == 1 ? 'checked' : ''}
                        />
                        <label class='custom-control-label' for={`${field}Input`} />
                    </div>
                    {saved[`${field}.${track.id}`] ? <div class={`valid-feedback`} style='display: block;'><Text id='tracks.changes-saved'>Endringene er lagret</Text></div> : 
                        <small id={`${field}Help`} class='form-text text-muted'>{help}</small>}
                </div>
            );
        }
        return (
            <div class={`form-group ${saved[`${field}.${track.id}`] ? 'is-valid' : ''}`}>
                <label for={`${field}Input`}>{icon && <i class={`${icon} text-muted`} />} {title}</label>
                <input type={type} class={`form-control ${saved[`${field}.${track.id}`] ? 'is-valid' : ''}`} id={`${field}Input`} aria-describedby={`${field}Help`}
                    value={value}
                    data-id={track.id}
                    data-field={field}
                    onInput={onInput}
                />
                {saved[`${field}.${track.id}`] ? <div class={`valid-feedback`} style='display: block;'><Text id='tracks.changes-saved'>Endringene er lagret</Text></div> : 
                    <small id={`${field}Help`} class='form-text text-muted'>{help}</small>}
            </div>
        );
    }
}

@observer
class InputNew extends Component {
    onInput = (e) => {
        const { field } = this.props;
        const value = e.target.value;
        const { trackStore } = this.props.stores;
        // console.log('trackStore.newTrack', trackStore.newTrack);
        trackStore.updateObjectKeyValue('newTrack', field, value);
    }

    render() {
        const { field, icon, title, help } = this.props;
        const { trackStore } = this.props.stores;
        const { newTrack } = trackStore;
        return (
            <div class='form-group'>
                <label for={`${field}Input`}>{icon && <i class={`${icon} text-muted`} />} {title}</label>
                <input type='text' class={`form-control`} id={`${field}Input`} aria-describedby={`${field}Help`}
                    onInput={this.onInput}
                    value={newTrack[field]}
                />
                <small id={`${field}Help`} class='form-text text-muted'>{help}</small>
            </div>
        );
    }
}

@observer
class TracksEdit extends Component {
    constructor(props) {
        super(props);
        this.state = {
            ...initialState,
        };
        this.updateTimer = {};
    }

    async loadTrack() {
		const { trackStore } = this.props.stores;
        if (this.props.track === 'new') {
            this.setState({ createNew: true });
        } else {
            trackStore.load(this.props.track);
        }
    }

	resetTrack() {
        const { trackStore } = this.props.stores;
        trackStore.updateKeyValue('newTrack', {});
	}

	insertTrack = async () => {
		const { trackStore, userStore } = this.props.stores;
		const { newTrack } = trackStore;
		await trackStore.insert({ ...newTrack });
        await userStore.getInfo();
        this.resetTrack();
        route(`/tracks/`);
	}

    updateField = (e) => {
        const { id, field, type, elementtype, isNew } = e.target.dataset;
        const value = e.target.value;
        this.updateFieldValue({ id: parseInt(id, 10), field, value, type, elementtype, isNew });
    }

    updateFieldValue = ({ id, field, value, datatype, elementtype, isNew = false, skipTimer = false }) => {
        const { trackStore } = this.props.stores;
        let newValue = value;

        let localObjectValue = value;
        const { track } = trackStore;
        if (field === 'images') {
            if (Array.isArray(track.images) && Array.isArray(value)) {
                localObjectValue = [...track.images, ...value];
            }
        }
    
        if (datatype === 'array' && !Array.isArray(value)) {
            if (elementtype === 'number') {
                newValue = value.split(',').map(e => parseFloat(e) || e);
            } else  {
                newValue = value.split(',');
            }
            // console.log('updateFieldValue', id, field, value, datatype, elementtype);
        }

        if (isNew) {
            trackStore.updateObjectKeyValue('newTrack', field, value);
        } else {
            trackStore.updateField(id, field, localObjectValue);
            if (skipTimer) {
                this.doUpdateField(id, field, newValue);
            } else {
                clearTimeout(this.updateTimer[field]);
                this.updateTimer[field] = setTimeout(async () => {
                    this.doUpdateField(id, field, newValue);
                }, 500);
            }
        }
    }

    async doUpdateField(id, field, value) {
        const { trackStore } = this.props.stores;
        await trackStore.saveField(id, field, value);
    }

    back = () => {
        const { history } = this.props.stores;
        history.goBack();
    }

	componentDidMount() {
        this.loadTrack();
    }

	render() {
        const { createNew } = this.state;
		const { trackStore } = this.props.stores;
		const { insertStatus, saved, track } = trackStore;
        const { name, gpx, gpxInfo } = track;
		return (
			<div class='container-fluid' style={`margin-bottom: ${MARGIN_BOTTOM}; margin-top: ${MARGIN_TOP};`}>
                <button class='btn btn-link pl-0 fixed-top text-primary'
                    style={`top: ${MARGIN_TOP_BACK}; left: 5px; font-size: 35px; width: 45px;`}
                    onClick={this.back}>
                    <i class='fas fa-arrow-circle-left' />
                </button>
				<div class='row'>
					<div class='offset-0 col-12 offset-sm-2 col-sm-8 offset-lg-4 col-lg-4' style='padding-left: 35px;'>
                        {createNew ? (
                            <div> 
                                <h5><Text id='tracks.add-new'>Legg til ny treningsrunde</Text></h5>
                                <InputNew stores={this.props.stores} field='name' icon='fas fa-heading'
                                    title={<Text id='tracks.title'>Tittel</Text>}
                                    help={<Text id='tracks.title-help'>Tittel på denne runden. Eks: 'Rundt Veslevann og tilbake'</Text>} />
                                <InputNew stores={this.props.stores} field='distanceKm' icon='fas fa-road'
                                    title={<Text id='tracks.distance'>Distanse i km</Text>}
                                    help={<Text id='tracks.distance-help'>Hvor lang er runden i km.</Text>} />
                                <InputNew stores={this.props.stores} field='elevation' icon='fas fa-mountain'
                                    title={<Text id='tracks.elevation'>Høydemeter</Text>}
                                    help={<Text id='tracks.elevation-help'>Høydemeter du klatrer på denne runden.</Text>} />

                                <button type='button' class='btn btn-primary'
                                    onClick={this.insertTrack}
                                >
                                    <Text id='tracks.save'>Registrer ny treningsrunde</Text>
                                </button>
                                <p class='mt-4'>
                                    <Text id='tracks.gpx-info'>For å legge til GPX må du lagre og gå tilbake og endre denne.</Text>
                                </p>
                            </div>
                        ) : (
                            <div>
                                <h5><Text id='tracks.edit'>Endre</Text>: {name}</h5>
                                <Input saved={saved} track={track} field='public' icon='fas fa-users' type='toggle'
                                    title={<Text id='tracks.public'>Offentlig</Text>}
                                    help={<Text id='tracks.public-help'>Er denne runden synlig for alle som bruker appen?</Text>}
                                    onInput={this.updateField} />
                                <Input saved={saved} track={track} field='name' icon='fas fa-heading'
                                    title={<Text id='tracks.title'>Tittel</Text>}
                                    help={<Text id='tracks.title-help'>Tittel på denne runden. Eks: 'Rundt Veslevann og tilbake'</Text>}
                                    onInput={this.updateField} />

                                <div class='row'>
                                    <div class='col'>
                                        <Input stores={this.props.stores} saved={saved} track={track} field='date' icon='fas fa-calendar' type='date' min='2010-01-01' max='today'
                                            title={<Text id='tracks.date'>Dato</Text>}
                                            help={<Text id='tracks.date-help'>Dato for turen. Format på dato er: 'YYYY-MM-DD'.</Text>}
                                            onInput={this.updateField}
                                            updateField={this.updateFieldValue}
                                        />
                                    </div>
                                    <div class='col'>
                                        <Input stores={this.props.stores} saved={saved} track={track} field='time' icon='fas fa-clock' type='time'
                                            title={<Text id='tracks.time'>Klokka</Text>}
                                            help={<Text id='tracks.time-help'>Klokkeslett for turen. Format: 'HH:MI'.</Text>}
                                            onInput={this.updateField}
                                            updateField={this.updateFieldValue}
                                        />
                                    </div>
                                </div>

                                <Input saved={saved} track={track} field='distanceKm' icon='fas fa-road'
                                    title={<Text id='tracks.distance'>Distanse i km</Text>}
                                    help={<Text id='tracks.distance-help'>Hvor lang er runden i km.</Text>}
                                    onInput={this.updateField} />
                                <Input saved={saved} track={track} field='elevation' icon='fas fa-mountain'
                                    title={<Text id='tracks.elevation'>Høydemeter</Text>}
                                    help={<Text id='tracks.elevation-help'>Høydemeter du klatrer på denne runden.</Text>}
                                    onInput={this.updateField} />
                                <Input saved={saved} track={track} field='image' icon='fas fa-camera' type='image'
                                    stores={this.props.stores}
                                    title={<Text id='tracks.photos'>Bilder</Text>}
                                    help={<Text id='tracks.photos-help'>Bilder fra denne runden.</Text>}
                                    onInput={this.updateField} updateField={this.updateFieldValue} />

                                {gpx && <div style='height: 500px;'>
                                    <Map stores={this.props.stores} mapId={track.id} geojson={gpx.geoJSON} gpxInfo={gpx.gpxInfo || gpxInfo} />
                                </div>}
                                <Input saved={saved} track={track} field='gpx' type='file'
                                    title={<Text id='tracks.gpx'>Gpx</Text>}
                                    help={<Text id='tracks.gpx-help'>Gpx til denne runden.</Text>}
                                    onInput={this.updateField} updateField={this.updateFieldValue} />

                            </div>                                                        
                        )}
					</div>

					{insertStatus && <div class='alert alert-success mt-4 col-12' role='alert'>
						<Text id='tracks.saved'>Runden er lagret.</Text>
					</div>}
				</div>
			</div>
		);
	}
}

export default TracksEdit;
